/*Sidebar Styles*/
.menuIcon {
    margin: 20px 85px;
    cursor: pointer;
}
.sideBar {
    display: flex;
    position: absolute;
    margin-left: 0px;
    flex-direction: column;
    top: 0px;
    left: 0px;
    height: 100%;
    background: none repeat scroll 0 0 white;
    color: white;
    font-family: "Open Sans";
    font-weight: bold;
    padding: 20px 20px 20px 50px;
    border-radius: 0px 15px 15px 0px;
    box-shadow: 0 8px 8px -1px rgba(0,0,0,.2) !important;
}

.sideBar ul {
    margin: 0;
    padding: 0;
}

.sideBar ul a {
    text-decoration: none;
}

.sideBar ul a li.active {
    background: #A78EC3;
    margin-left: 0;
    padding-left: 20px;
    text-decoration: none;
}
.sideBar ul a li.active span{
    color: white;
    text-decoration: none;
    text-align: left;
    margin-left: 20px;
    margin-right: 12px;
}
.sideBar ul a li {
    margin: 10px;
    padding: 10px 10px 10px 0px;
    margin-top: 10px;
    border-radius: 0px 15px 15px 0px;
    list-style-type: none;
    list-style-position: outside;
    text-decoration: none;
}

.sideBar ul a li span{
    text-decoration: none;
    text-align: left;
    color: #919699;
    margin-left: 20px;
    margin-right: 12px;
    padding-left: 0px;
    font-size: 16px;
    position: relative;
} 

.sideBar ul li a:hover:not(.active) {
    background: #A78EC3;
    box-shadow: 0 20px 20px -2px rgba(0,0,0,.2);
    color:white;
    margin-left: 0;
    padding-left: 20px;
    text-decoration: none;
}


.sideBar ul a li:hover span {
    color: inherit;
}

.sideBar ul a:hover {
    text-decoration: none;
}

.sideBar ul a li, span:hover {
    color: white;
    text-decoration: none;
    text-align: left;
    margin-left: 20px;
    margin-right: 12px;
}
